import styled from 'styled-components';
import Layout from '../../components/Layout';
import {TagsSection} from './TagSection';
import {NotesSection} from './NotesSection';
import {CategorySection} from './CategorySection';
import {NumberSection} from './NumberSection';
import React, {useState} from 'react';
import {useRecords} from '../../hooks/useRecords';
import day from 'dayjs';

const MyLayout = styled(Layout)`
  display: flex;
  flex-direction: column;
`;
type Category = '-' | '+';
const defaultFromDate = {
  tags: [] as number[],
  note: '',
  category: '-' as Category,
  amount: '0',
  createdAt: day(new Date()).format('YYYY-MM-DD')
};

function Money() {
  const [selected, setSelected] = useState(defaultFromDate);
  const onChange = (obj: Partial<typeof selected>) => {
    setSelected({...selected, ...obj});
  };
  const onChangeDate = (obj: Partial<typeof selected>) => {
    setSelected({...selected, ...obj});
  };
  
  const {addRecord} = useRecords();
  const submit = () => {
    if (addRecord(selected)) {
      alert('');
      setSelected(defaultFromDate);
    }
  };
  return (
    <MyLayout scrollTop={9999}>
      <TagsSection
        value={selected.tags}
        onChange={(tags) => {
          onChange({tags});
        }}
      />
      <NotesSection
        value={selected.note}
        createdAt={selected.createdAt}
        onChange={(note) => {
          onChange({note});
        }}
        onChangeDate={(createdAt) => {
          onChangeDate({createdAt});
        }}
      />
      <CategorySection
        value={selected.category}
        onChange={(category) => {
          onChange({category});
        }}
      />
      <NumberSection
        value={selected.amount}
        onChange={(amount) => {
          onChange({amount});
        }}
        onSubmit={submit}
      />
    </MyLayout>
  );
}

export default Money;
