import React, { useEffect, useRef } from 'react';
import * as echarts from 'echarts';
import styled from 'styled-components';
type Props = {
	option: any[];
	loading: boolean;
};
const EchartWrapper = styled.div`
	overflow: auto;
	background: #fff;
	box-shadow: inset 0 -5px 5px -5px rgba(0, 0, 0, 0.25), inset 0 5px 5px -5px rgba(0, 0, 0, 0.25);
	::-webkit-scrollbar {
		display: none;
	}
	> div {
		width: 430%;
	}
`;
export function ReactEcharts(props: Props) {
	const container = useRef<HTMLDivElement>(null);
	const chart = useRef<any>(null);
	const wrapper = useRef<HTMLDivElement>(null);
	const { option, loading } = props;
	useEffect(() => {
		if (container.current) {
			container.current.style.height = `400px`;
			chart.current = echarts.init(container.current, 'drak');
		}
	}, []);
	useEffect(() => {
		if (wrapper.current) {
			wrapper.current.scrollLeft = wrapper.current.scrollWidth;
		}
		chart.current && chart.current.setOption(option);
	}, [option]);
	useEffect(() => {
		if (loading) {
			chart.current && chart.current.showLoading();
		} else {
			chart.current && chart.current.hideLoading();
		}
	}, [loading]);
	return (
		<EchartWrapper ref={wrapper}>
			<div ref={container}></div>
		</EchartWrapper>
	);
}
