import React, {ChangeEventHandler} from 'react';
import styled from 'styled-components';
import {Input} from '../../components/Input';
import day from 'dayjs';

const Wrapper = styled.section`
  background-color: #f5f5f5;
  padding: 14px 16px;
  font-size: 14px;

  .bb {
    border-bottom: 1px solid #cdcdcd;
  }
`;
type Props = { value: string; createdAt: string; onChange: (value: string) => void; onChangeDate: (value: string) => void };
const NotesSection: React.FC<Props> = (props) => {
  const note = props.value;
  const createdAt = props.createdAt;
  const maxDate = day(new Date()).format('YYYY-MM-DD');
  const onChange: ChangeEventHandler<HTMLInputElement> = (e) => {
    props.onChange(e.target.value);
  };
  const onChangeDate: ChangeEventHandler<HTMLInputElement> = (e) => {
    props.onChangeDate(e.target.value);
  };
  return (
    <Wrapper>
      <Input className="bb" label="" type="text" placeholder="" value={note} onChange={onChange}/>
      <Input label="" type="date" placeholder="" value={createdAt} onChange={onChangeDate} max={maxDate}/>
    </Wrapper>
  );
};
export {NotesSection};
