import React, { useState} from 'react';
import {HashRouter as Router, Switch, Route, Redirect} from 'react-router-dom';
import styled from 'styled-components';
import Money from './views/Money/index';
import NoMatch from './views/NoMatch';
import Statistics from './views/Statistics/index';
import Tags from './views/Tags/index';
import Tag from './views/Tags/Tag';
import CodeMask from './components/CodeMask';

const AppWrapper = styled.div`
  color: #333;
  max-width: 500px;
  margin: 0 auto;
`;

function App() {
  const [isTouch, setIsTouch] = useState<boolean>('ontouchstart' in document);
  if(!isTouch){
    alert("")
  }
  return (
    <AppWrapper>
      {!isTouch && <CodeMask onClick={(value) => {
        setIsTouch(value);
      }}/>}
      <Router>
        <Switch>
          <Route exact path="/tags/:id">
            <Tag/>
          </Route>
          <Route exact path="/tags">
            <Tags/>
          </Route>
          <Route exact path="/money">
            <Money/>
          </Route>
          <Route exact path="/statistics">
            <Statistics/>
          </Route>
          <Redirect exact from="/" to="/money"/>
          <Route path="/*">
            <NoMatch/>
          </Route>
        </Switch>
      </Router>
    </AppWrapper>
  );
}

export default App;
