import React, {ReactNode, useEffect, useState} from 'react';
import {ReactEcharts} from '../../components/ReactEcharts';
import Layout from '../../components/Layout';
import {useRecords, RecordItem} from '../../hooks/useRecords';
import {useTags} from '../../hooks/useTags';
import {CategorySection} from '../Money/CategorySection';
import day from 'dayjs';
import styled from 'styled-components';

const Item = styled.div`
  display: flex;
  justify-content: space-between;
  background: #fff;
  font-size: 18px;
  line-height: 20px;
  padding: 10px 16px;
  /* &:not(:last-child) {
      border-bottom: 1px solid #d5d5d5;
    } */
  border-top: 1px solid #d5d5d5;

  > .note {
    margin-right: auto;
    margin-left: 16px;
    color: #9b9b9b;
  }
`;
const Header = styled.h3`
  font-size: 18px;
  line-height: 20px;
  padding: 10px 16px;
  display: flex;
  justify-content: space-between;
  .red{
    color: orangered;
  }
  .green{
    color: rgb(134,234,98);
  }
`;
type Category = '-' | '+';

function Statistics() {
  const [category, setCategory] = useState<Category>('-');
  const {records} = useRecords();
  const {getName} = useTags();
  const [loading] = useState(false);
  const [list, setList] = useState<any[]>([]);
  const [option, setOption] = useState<any>({});
  
  function getOption(x: string[], y: (string | number)[]) {
    return {
      xAxis: {
        type: 'category',
        axisTick: {alignWithLabel: true},
        data: x
      },
      yAxis: {
        type: 'value',
        show: false
      },
      grid: {
        left: 0,
        right: 0
      },
      tooltip: {
        show: true,
        triggerOn: 'click',
        formatter: '{c}',
        position: 'top'
      },
      series: [
        {
          type: 'line',
          symbol: 'circle',
          symbolSize: 12,
          itemStyle: {
            color: '#6181b1'
          },
          label: {
            show: true,
            position: 'top'
          },
          data: y
        }
      ]
    };
  }
  
  // 
  const sumArr = (arr: number[]) => {
    let sum = 0;
    for (let i = 0; i < arr.length; i++) {
      sum += arr[i] * 10;
    }
    return sum / 10;
  };
  useEffect(() => {
    const selectedRecords = records.filter((r) => r.category === category);
    const hash: { [K: string]: RecordItem[] } = {}; // {'yyyy-mm-dd',[item,item]}
    selectedRecords.map((r) => {
      const key = day(r.createdAt).format('YYYYMMDD');
      if (!(key in hash)) {
        hash[key] = [];
      }
      hash[key].push(r);
      return '';
    });
    const _list = Object.entries(hash).sort((a, b) => {
      if (a[0] === b[0]) return 0;
      if (a[0] > b[0]) return -1;
      if (a[0] < b[0]) return 1;
      return 0;
    });
    setList(_list);
    // echart
    const today = new Date();
    const echartArr = [];
    for (let i = 0; i <= 29; i++) {
      const dateString = day(today).subtract(i, 'day').format('YYYY-MM-DD');
      const found = selectedRecords.map((e) => {
        return e.createdAt === dateString ? parseFloat(e.amount) : 0;
      });
      echartArr.push({date: dateString, value: sumArr(found) > 0 ? sumArr(found) : 0});
    }
    echartArr.sort((a, b) => {
      if (a > b) {
        return 1;
      } else if (a === b) {
        return 0;
      } else {
        return -1;
      }
    });
    const keys = echartArr.map((r) => day(r.date).format('MM-DD'));
    const values = echartArr.map((r) => r.value);
    setOption(getOption(keys, values));
  }, [records, category]);
  return (
    <Layout>
      <CategorySection
        value={category}
        onChange={(category) => {
          setCategory(category);
        }}
      />
      <ReactEcharts option={option} loading={loading}/>
      {list.map(([date, records]) => (
        <div key={date}>
          <Header>
            <span>{date}</span>
            <span><span className={category==='-'?'red':'green'}>{sumArr(records.map((a: { amount: string }) => parseFloat(a.amount)))}</span></span>
          </Header>
          <div>
            {records.map((r: { id: number; tags: []; note: string; amount: string }) => (
              <Item key={r.id}>
                <div className="tags">
                  {r.tags
                  .map((id: number) => <span key={id}>{getName(id)}</span>)
                  .reduce((res, span, index, array) => res.concat(index < array.length - 1 ? [span, ''] : [span]), [] as ReactNode[])}
                </div>
                {r.note && <div className="note">{r.note}</div>}
                <div className="amount">{r.amount}</div>
              </Item>
            ))}
          </div>
        </div>
      ))}
    </Layout>
  );
}

export default Statistics;
