import React, { useState } from 'react';
import { Wrapper } from './NumberSection/Wrapper';
import { generateOutput } from './NumberSection/generateOutput';

type Props = { value: string; onChange: (value: string) => void; onSubmit: () => void };
const NumberSection: React.FC<Props> = (props) => {
	const [buttonList] = useState<string[]>(['1', '2', '3', '', '4', '5', '6', '', '7', '8', '9']);
	const output = props.value.toString();
	const setOutput = (output: string) => {
		let temp: string = '0';
		if (output.length > 16) {
			temp = output.slice(0, 16);
		} else {
			temp = output;
		}
		props.onChange(temp);
	};
	const onClickButtonWrapper = (e: React.MouseEvent<HTMLDivElement>) => {
		const text = (e.target as HTMLButtonElement).textContent;
		if (text === null) {
			return;
		}
		if (text === 'OK') {
			props.onSubmit();
			return;
		}
		if ('0123456789.'.split('').concat(['', '']).indexOf(text) >= 0) {
			// 
			setOutput(generateOutput(text, output));
		}
	};
	return (
		<Wrapper>
			<div className="output">{output}</div>
			<div className="pad clearfix" onClick={onClickButtonWrapper}>
				{buttonList.map((text) => (
					<button key={text}>{text}</button>
				))}
				<button className="ok">OK</button>
				<button className="zero">0</button>
				<button className="dot">.</button>
			</div>
		</Wrapper>
	);
};
export { NumberSection };
