import { useEffect, useState } from 'react';
import { useUpdate } from './useUpdate';
export type RecordItem = {
	id: number;
	tags: number[];
	note: string;
	category: '-' | '+';
	amount: string;
	createdAt: string; //IOS 8601
	// updateAt: string;
};
type NewRecordItem = Omit<RecordItem, 'id'>;

export const useRecords = () => {
	const [records, setRecords] = useState<RecordItem[]>([]);
	useEffect(() => {
		setRecords(JSON.parse(window.localStorage.getItem('records') || '[]'));
	}, []);
	useUpdate(() => {
		window.localStorage.setItem('records', JSON.stringify(records));
	}, records);
	const addRecord = (newRecord: NewRecordItem) => {
		if (parseInt(newRecord.amount) <= 0) {
			alert('');
			return false;
		}
		if (newRecord.tags.length === 0) {
			alert('');
			return false;
		}
		const newId = records.length > 0 ? records[records.length - 1].id + 1 : 0;
		const record = { ...newRecord, id: newId };
		setRecords([...records, record]);
		return true;
	};
	return { records, addRecord };
};
