import React, {useState} from 'react';
import styled from 'styled-components';

const Wrapper = styled.section`
  font-size: 24px;

  > ul {
    display: flex;
    background-color: #fff;

    > li {
      width: 50%;
      text-align: center;
      padding: 16px 0;
      position: relative;

      &.selected::after {
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 0;
        content: '';
        display: block;
        background-color: #ffd155;
        height: 3px;
      }
    }
  }
`;
type Category = '-' | '+';
type Props = { value: Category; onChange: (value: Category) => void };
const CategorySection: React.FC<Props> = (props) => {
  const categoryMap = {'-': '', '+': ''};
  // type keys = keyof typeof categoryMap;
  const [categoryList] = useState<Category[]>(['-', '+']);
  const category = props.value;
  return (
    <Wrapper>
      <ul>
        {categoryList.map((r) => (
          <li
            className={category === r ? 'selected' : ''}
            key={r}
            onClick={() => {
              props.onChange(r);
            }}
          >
            {categoryMap[r]}
          </li>
        ))}
      </ul>
    </Wrapper>
  );
};
export {CategorySection};
